.tool-button {
    width: 40px;
    opacity: .2;
    background-color: rgba(255, 255, 255, 0);
    border: none;
    outline: none;
    cursor: pointer;
    box-sizing: border-box;
    margin: 0 0 .7em 0; }

.tool-button:first-of-type {
    margin: 0 0 2em 0; }

.tool-button_active {
    opacity: 1; }
